<template>
  <div id='app'>
    <div class='app-phone'>
      <div class='phone-Header'>
        <img src='./assets/vue_gram_logo_cp.png' alt='logo'>
        <a class='cancel-cta' v-if='step===2 || step===3' @click='goToHome'>Cancel</a>
        <a class='next-cta' v-if='step===2' @click='step++'>Next</a>
        <a class='next-cta' v-if='step===3' @click='sharePost'>share</a>
      </div>
      <!-- //phone-Header -->
      <phone-body
        :step='step'
        :posts='posts'
        :filters='filters'
        :image='image'
        :selectedFilter='selectedFilter'
        v-model='caption'
      ></phone-body>
      <!-- //phone-body -->
      <div class='phone-Footer'>
        <div class='home-cta' @click='goToHome'>
          <i class='fas fa-home'></i>
        </div>
        <div class='update_cta'>
          <input
            type='file'
            name='file'
            id='file'
            class='inputfile'
            @change='uploadImage'
            :disabled='step!==1'
          >
          <!-- step2,3 파일업로드를 비활성화 하기
          disable하다 step!==1 step1이 아닐 경우
          input을 통패 files선택함 change 이벤트 발생-->
          <label for='file' id='file'>
            <i class='fas fa-plus-square'></i>
          </label>
        </div>
      </div>
      <!-- //phone-footer -->
    </div>
    <!-- //app-phone -->
  </div>
  <!-- //app -->
</template>
<script>
// 객체생성
import PhoneBody from './components/PhoneBody.vue'
import posts from './data/posts.js'
import filters from './data/filters.js'

import EventBus from './event-bus.js'

export default {
  name: 'App',
  data () {
    return {
      step: 1,
      posts,
      filters,
      image: '',
      selectedFilter: '',
      caption: ''
    }
  },
  created () {
    EventBus.$on('filter-selected', evt => {
      this.selectedFilter = evt.filter
    })
  },
  methods: {
    goToHome () {
      this.image = ''
      this.selectedFilter = ''
      this.caption = ''
      this.step = 1
    },
    sharePost () {
      const post = {
        username: '정유정',
        userImage: 'https://s3-us-west-2.amazonaws.com/s.cdpn.io/1211695/me_3.jpg',
        postImage: this.image,
        likes: 0,
        caption: this.caption,
        filter: this.selectedFilter
      }
      this.posts.unshift(post)
      this.goToHome()
    },
    uploadImage (evt) {
      const files = evt.target.files
      if (!files.length) return
      const reader = new FileReader()
      reader.readAsDataURL(files[0])
      reader.onload = evt => {
        this.image = evt.target.result
        this.step = 2
      }
      document.querySelector('#file').value = ''
    }
  },
  components: {
    'phone-body': PhoneBody
  }
}
</script>

<style lang='scss' src='./styles/app.scss'>
</style>
