<template>
  <div class="filter-type">
    <p>{{filter.name}}</p>
    <!-- filterss라는 객체에 name을 불러와라 -->
    <div
      class="img"
      :class="filter.name"
      :style="{backgroundImage: 'url('+image+')'}"
      @click="selectFilter"
    ></div>
  </div>
  <!-- // filter-type-->
  <!--:class="filter.name"을 지정>클릭시 class가 지정해 붙음  -->
</template>

<script>
import EventBus from '../event-bus.js'
export default {
  name: 'filterType',
  props: {
    filter: Object,
    image: String
  },
  methods: {
    selectFilter () {
      EventBus.$emit('filter-selected', { filter: this.filter.name })
    }
  }
}
</script>

<style lang="scss" src="../styles/filter-type.scss">
</style>
