<template>
  <div class="phone-body">
    <div v-if="step===1" class="feed" v-dragscroll.y>
      <!-- step===1이면 classs=feed가 나와라 -->
      <vuegram-post v-for="(post, index) in posts" :post="post" :key="index"></vuegram-post>
      <!-- posts라는 객체 안에 post 각각 하나하나 index -->
    </div>

    <div v-if="step===2">
      <div
        class="selected-image"
        :class="selectedFilter"
        :style="{backgroundImage:'url('+image+')'}"
      ></div>

      <div class="filter-container" v-dragscroll.x>
        <filter-type v-for="(filter,index) in filters" :filter="filter" :image="image" :key="index"></filter-type>
      </div>
    </div>

    <div v-if="step===3">
      <div
        class="selected-image"
        :class="selectedFilter"
        :style="{backgroundImage:'url('+image+')'}"
      ></div>
      <div class="caption-container">
        <textarea
          class="caption-input"
          placeholder="white a caption"
          type="text"
          :value="value"
          @input="$emit('input',$event.target.value)"
        ></textarea>
      </div>
    </div>
  </div>
</template>
<script>
import VuegramPost from './VuegramPost.vue'
import FilterType from './FilterType.vue'
export default {
  name: 'PhoneBody',
  props: {
    step: Number,
    posts: Array,
    selectedFilter: String,
    image: String,
    filters: Array,
    value: String
  },
  components: {
    'vuegram-post': VuegramPost,
    'filter-type': FilterType
  }
}
</script>
<style lang="scss" src="../styles/phone-body.scss">
</style>
